import { Component } from '@angular/core';
import { NotificationService } from '../../services/notification.service';
import { notificationAnimation } from '../../common/animations';
import { notificationConfig } from '../../common/notificationConfig';
import {
    animate,
    keyframes,
    state,
    style,
    transition,
    trigger,
} from '@angular/animations';

@Component({
    selector: 'skr-success-notification',
    template: `
        <div class="notification" [@openClose]="hidden ? 'void' : 'open'">
            <div class="icon-container">
                <skr-close-icon></skr-close-icon>
            </div>
            <div>{{ message }}</div>
            <div class="close-icon-container" (click)="handleClose()">
                <skr-close-icon></skr-close-icon>
            </div>
        </div>
    `,
    styles: [
        `
            .notification {
                position: relative;

                display: grid;
                grid-gap: 1rem;
                grid-template-columns: auto 1fr;
                background-color: var(--color-surface);
                padding: 1.5rem 3.5rem 1.5rem 1.5rem;
                width: 25rem;
                align-items: center;
                border-radius: 0.8rem;
                border-left: 0.4rem solid var(--color-accent-2);
                box-shadow: 0 0.8rem 2rem var(--color-big-shadow);
            }

            .icon-container {
                display: grid;
                border: 0.1rem solid currentColor;
                color: var(--color-accent-2);
                border-radius: 50%;
                width: 1.6rem;
                height: 1.6rem;
                align-content: end;
                justify-content: center;
            }

            .close-icon-container {
                position: absolute;
                top: 0.5rem;
                right: 1rem;
                width: 20px;
                color: var(--color-on-surface-secondary);
            }
        `,
    ],
    animations: [
        trigger('openClose', [
            state(
                'void',
                style({
                    opacity: 0,
                })
            ),
            state(
                'open',
                style({
                    opacity: 1,
                })
            ),
            transition(
                'void => open',
                animate(
                    `${notificationConfig.appearanceTime}ms ease-out`,
                    keyframes([
                        style({
                            transform: 'translate3d(100%, 0, 0) skewX(-30deg)',
                            opacity: 0,
                        }),
                        style({
                            transform: 'skewX(20deg)',
                            opacity: 0.5,
                        }),
                        style({
                            transform: 'skewX(-5deg)',
                            opacity: 0.5,
                        }),
                        style({
                            transform: 'none',
                            opacity: 1,
                        }),
                    ])
                )
            ),
            transition(
                'open => void',
                animate(
                    `${notificationConfig.disappearanceTime}ms ease-out`,
                    keyframes([
                        style({
                            opacity: 1,
                        }),
                        style({
                            transform: 'translate3d(100%, 0, 0) skewX(30deg)',
                            opacity: 0,
                        }),
                    ])
                )
            ),
        ]),
    ],
})
export class SuccessNotificationComponent {
    public message = '';
    public notificationService: NotificationService;
    public hidden = false;

    public handleClose(): void {
        this.notificationService.detachComponent(this);
    }
}
